import Vue from 'vue'
import Router from 'vue-router'
const Login = () => import('@/components/Login.vue')
const ListView = () => import('@/components/List.vue')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/list',
      name:"ListView",
      component: ListView
    }
  ]
})



